import PropTypes from "prop-types";

const styles = {
    container: {
        borderColor: "#dee2e6",
        backgroundColor: "#fafafa",
        marginTop: "10px",
    },
};

const PricingSummary = ({ pricingInfo }) => {
    const { sgstAmount, cgstAmount, finalPriceWithGst, totalPriceAfterDiscount, totalItemsPrice } = pricingInfo;
    return (
        <div className="mt-4 border rounded p-3 mb-3" style={styles.container}>
            <h5 className="mb-3">Pricing Summary</h5>
            <div className="table-responsive">
                <table className="table table-bordered">
                    <tbody>
                        <tr>
                            <th>Total Items Price</th>
                            <td>{Number(totalItemsPrice || 0).toFixed(2)}</td>
                        </tr>
                        {totalPriceAfterDiscount && (
                            <tr>
                                <th>Price After Discount</th>
                                <td>{Number(totalPriceAfterDiscount || 0).toFixed(2)}</td>
                            </tr>
                        )}
                        <tr>
                            <th>SGST (9%)</th>
                            <td>{Number(sgstAmount || 0).toFixed(2)}</td>
                        </tr>
                        <tr>
                            <th>CGST (9%)</th>
                            <td>{Number(cgstAmount || 0).toFixed(2)}</td>
                        </tr>
                        <tr>
                            <th>Final Price (With GST)</th>
                            <td style={{ fontWeight: 'bold' }}>{Number(finalPriceWithGst || 0).toFixed(2)}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    );
};


PricingSummary.propTypes = {
    totalPrice: PropTypes.number.isRequired,
    sgstAmount: PropTypes.number.isRequired,
    cgstAmount: PropTypes.number.isRequired,
    finalPriceWithGst: PropTypes.number.isRequired,
};


export default PricingSummary;
