import './screen.style.css';

const styles = {
    container: {
        borderColor: "#dee2e6",
        backgroundColor: "#fafafa",
        marginTop: "10px",
    },
};

const Discount = ({ discountInfo, change }) => {
    return (
        <div className="row g-3 border rounded p-3 mb-3" style={styles.container}>
            {/* Any Discount? */}
            <div className="col-md-4">
                <label htmlFor="isAnyDiscount" className="form-label">
                    Any Discount?
                </label>
                <select
                    id="isAnyDiscount"
                    name="isAnyDiscount"
                    className="form-select"
                    value={discountInfo.isAnyDiscount || "Already discounted"}
                    onChange={change}
                >
                    <option value="Already discounted">Already discounted</option>
                    <option value="No">No</option>
                    <option value="Yes">Yes</option>
                </select>
            </div>

            {/* Discount (%) */}
            <div className="col-md-4">
                <label htmlFor="discount" className="form-label">
                    Discount (%)
                </label>
                <input
                    type="number"
                    id="discount"
                    name="discount"
                    className="form-control"
                    placeholder="Discount %"
                    min="0"
                    max="100"
                    step="0.01"
                    value={discountInfo.discount || ""}
                    onChange={change}
                    readOnly={discountInfo.isAnyDiscount !== 'Yes'}
                />
            </div>

            {/* Price After Discount */}
            <div className="col-md-4">
                <label htmlFor="priceAfterDiscount" className="form-label">
                    Price After Discount
                </label>
                <input
                    type="number"
                    id="priceAfterDiscount"
                    name="priceAfterDiscount"
                    className={`form-control ${discountInfo.priceAfterDiscount ? 'readonly-highlight input-field-text' : 'disabled-field'}`}
                    value={discountInfo.priceAfterDiscount || ""}
                    readOnly
                />
            </div>
        </div>
    );
};

export default Discount;
