import PropTypes from "prop-types";
import './../../Screens/screen.style.css';

const ItemBasicInfo = ({ itemBasicInfo, handleItemValueChange }) => {
    return (
        <>
            <div className="col-md-6">
                <label htmlFor="itemName" className="form-label">Item Name</label>
                <input type="text" id="itemName" name="itemName" value={itemBasicInfo.itemName} className="input-field-text form-control" placeholder="Item Name" required
                    onChange={handleItemValueChange}
                />
            </div>
            <div className="col-md-3">
                <label htmlFor="quantity" className="form-label">Quantity</label>
                <input type="number" id="quantity" name="quantity" value={itemBasicInfo.quantity} className="form-control" placeholder="Quantity" min="1"
                    required onChange={handleItemValueChange} />
            </div>
            <div className="col-md-3">
                <label htmlFor="amount" className="form-label">Amount (per unit)</label>
                <input type="number" id="amount" name="amount" value={itemBasicInfo.amount} className="form-control" placeholder="Amount" min="0"
                    step="0.01" required onChange={handleItemValueChange} />
            </div>
            <div className="col-md-6">
                <label htmlFor="totalPrice" className="form-label">Total Price</label>
                <input type="number" id="totalPrice" name="totalPrice" value={itemBasicInfo.totalPrice} className="form-control readonly-highlight input-field-text"
                    placeholder="Total Price" readOnly />
            </div>
            {/* Add upload item image option */}
            <div className="col-md-6">
                <label htmlFor="itemImage" className="form-label">Item Image</label>
                <input type="file" id="itemImage" name="itemImage" className="form-control" onChange={handleItemValueChange} />
            </div>
        </>
    )
}


ItemBasicInfo.propTypes = {
    itemBasicInfo: PropTypes.shape({
        itemName: PropTypes.string,
        quantity: PropTypes.number,
        amount: PropTypes.number,
        totalPrice: PropTypes.number,
    }).isRequired,
    handleItemValueChange: PropTypes.func.isRequired,
};

export default ItemBasicInfo;