import { useState, useEffect, useRef } from "react";
import CompanyInfo from "./../Screens/CompanyInfo"; // Adjust the path as necessary
import data from "./../core/data"; // Adjust the path as necessary
import SuccessPopupComponent from "./../common/components/SuccessPopup.component";
import CustomerDetail from "./../Screens/CustomerDetail";
import ItemDetails from "./../Screens/Items/ItemDetails";
import Discount from "./../Screens/Discount";
import PricingSummary from "./PricingSummary";
import Footer from "./../Screens/Footer"; // Adjust the path as necessary
import messageLabels from "./../core/messages.constant"; // Adjust the path as necessary
import generatePDF from "../core/services/pdf-generator.service";
import "./screen.style.css"; // Import your CSS file for styles
import * as bootstrap from 'bootstrap'; // Add this import at the top
import TermsAndConditions from "./TermsAndConditions";

const { companyInformation, termsAndConditions } = data; // Destructure to get company info and terms

function QuotationForm() {
    // Example state for customer info
    const [customerInfo, setCustomerInfo] = useState({
        name: "",
        phone: "",
        address: "",
    });
    // Change to support multiple items
    const [itemBasicInfoList, setItemBasicInfoList] = useState([
        {
            itemName: "",
            quantity: '',
            amount: '',
            totalPrice: '',
            itemImage: ''
        }
    ]);
    const [itemSpecificationsRowsList, setItemSpecificationsRowsList] = useState([
        [
            { detail: "", value: "" },
            { detail: "", value: "" },
            { detail: "", value: "" },
        ]
    ]);
    // Discount and pricing per item
    const [discountInfo, setDiscountInfo] = useState({
        isAnyDiscount: "Already discounted",
        discount: "",
        priceAfterDiscount: ""
    });
    const [pricing, setPricing] = useState({
        totalItemsPrice: 0,
        totalPriceAfterDiscount: 0,
        sgstAmount: 0,
        cgstAmount: 0,
        finalPriceWithGst: 0
    });

    // Add state for terms and conditions
    const [currentTermsAndConditions, setCurrentTermsAndConditions] = useState(termsAndConditions);

    // Calculate total price for all items
    const getTotalItemsPrice = (list = itemBasicInfoList) => {
        return list.reduce((sum, item) => {
            const price = parseFloat(item.totalPrice) || 0;
            return sum + price;
        }, 0);
    };

    // Centralized discount info calculation
    const recalculateDiscountInfo = (prevDiscountInfo, totalItemsPrice) => {
        const discountPercent = Number(prevDiscountInfo.discount || 0);
        let price;
        if (prevDiscountInfo.isAnyDiscount === 'Yes') {
            price = totalItemsPrice - (totalItemsPrice * discountPercent) / 100;
        }
        return { ...prevDiscountInfo, priceAfterDiscount: price ? price.toFixed(2) : "" };
    };

    const handleDiscountChange = (e) => {
        const { name, value } = e.target;
        setDiscountInfo((prev) => {
            let val = value;
            // Limit discount to 0100
            if (name === "discount") {
                val = Math.min(Math.max(Number(value), 0), 100);
            }
            const next = { ...prev, [name]: val };
            const totalItemsPrice = getTotalItemsPrice();
            if (next.isAnyDiscount === 'Yes' && next.discount) {
                return recalculateDiscountInfo(next, totalItemsPrice);
            } else {
                return { ...next, priceAfterDiscount: "", discount: "" };
            }
        });
    };

    // Example handler for input change
    const handleCustomerChange = (e) => {
        setCustomerInfo({ ...customerInfo, [e.target.name]: e.target.value });
    };

    // ItemDetails handlers now take an index
    const handleItemValueChange = (itemIdx, e) => {
        const { name, value, files } = e.target;
        setItemBasicInfoList((prev) => {
            const next = prev.map((item, idx) =>
                idx === itemIdx ? { ...item, [name]: name === "itemImage" && files && files[0] ? URL.createObjectURL(files[0]) : value } : item
            );
            // Recompute totalPrice for this item
            const quantity = next[itemIdx].quantity;
            const amount = next[itemIdx].amount;
            let total = (quantity * amount).toString();
            next[itemIdx].totalPrice = Number(total).toFixed(2);

            // Update discountInfo.priceAfterDiscount for all items
            const totalItemsPrice = getTotalItemsPrice(next);
            setDiscountInfo((prev) => recalculateDiscountInfo(prev, totalItemsPrice));
            return next;
        });
    };

    // Item specification handlers now take an item index
    const handleItemSpecificationDeleteRow = (itemIdx, rowIdx) => {
        setItemSpecificationsRowsList((prev) =>
            prev.map((rows, idx) =>
                idx === itemIdx ? rows.filter((_, i) => i !== rowIdx) : rows
            )
        );
    };

    const handleItemSpecificationAddRow = (itemIdx) => {
        setItemSpecificationsRowsList((prev) =>
            prev.map((rows, idx) =>
                idx === itemIdx ? [...rows, { detail: "", value: "" }] : rows
            )
        );
    };

    const handleItemSpecificationChange = (itemIdx, rowIdx, e) => {
        const { name, value } = e.target;
        setItemSpecificationsRowsList((prev) =>
            prev.map((rows, idx) =>
                idx === itemIdx
                    ? rows.map((row, i) =>
                        i === rowIdx ? { ...row, [name]: value } : row
                    )
                    : rows
            )
        );
    };

    // Add more items
    const handleAddMoreItems = () => {
        setItemBasicInfoList((prev) => [
            ...prev,
            { itemName: "", quantity: '', amount: '', totalPrice: '' }
        ]);
        setItemSpecificationsRowsList((prev) => [
            ...prev,
            [
                { detail: "", value: "" },
                { detail: "", value: "" },
                { detail: "", value: "" },
            ]
        ]);
    };

    // Delete item row
    const handleDeleteItemRow = (idx) => {
        setItemBasicInfoList((prev) => {
            const next = prev.filter((_, i) => i !== idx);
            // Recalculate discountInfo after item deletion
            const totalItemsPrice = getTotalItemsPrice(next);
            setDiscountInfo((prev) => recalculateDiscountInfo(prev, totalItemsPrice));
            return next;
        });
        setItemSpecificationsRowsList((prev) => prev.filter((_, i) => i !== idx));
    };
    // Pricing calculation (global)
    useEffect(() => {
        const totalItemsPrice = getTotalItemsPrice();
        const priceAfterDiscount = discountInfo.priceAfterDiscount
            ? Number(discountInfo.priceAfterDiscount)
            : totalItemsPrice;
        const sgst = priceAfterDiscount * 0.09;
        const cgst = priceAfterDiscount * 0.09;
        const finalPrice = priceAfterDiscount + sgst + cgst;
        setPricing({
            totalItemsPrice: totalItemsPrice,
            totalPriceAfterDiscount: discountInfo.priceAfterDiscount ? priceAfterDiscount : '',
            sgstAmount: sgst,
            cgstAmount: cgst,
            finalPriceWithGst: finalPrice.toFixed(2),
        });
    }, [discountInfo.priceAfterDiscount, itemBasicInfoList]);

    // Utility: Find and mark invalid inputs
    const markInvalidInputs = () => {
        const form = document.querySelector('form');
        if (!form) return;
        // Remove previous invalid styles from all form controls
        form.querySelectorAll('.is-invalid').forEach(el => el.classList.remove('is-invalid'));
        // Add is-invalid to all invalid fields
        form.querySelectorAll(':invalid').forEach(el => el.classList.add('is-invalid'));
    };

    // Remove is-invalid class on input if it becomes valid
    useEffect(() => {
        const form = document.querySelector('form');
        if (!form) return;
        const handler = (e) => {
            if (e.target && e.target.classList && e.target.classList.contains('is-invalid')) {
                if (e.target.checkValidity()) {
                    e.target.classList.remove('is-invalid');
                }
            }
        };
        form.addEventListener('input', handler, true);
        form.addEventListener('change', handler, true);
        return () => {
            form.removeEventListener('input', handler, true);
            form.removeEventListener('change', handler, true);
        };
    }, []);

    // Handler for terms and conditions change
    const handleTermsChange = (updatedTerms) => {
        setCurrentTermsAndConditions(updatedTerms);
    };

    // PDF generation handler: collect all items
    const handleGeneratePDF = async (e) => {
        e.preventDefault();
        // Check form validity
        const form = e.target.closest('form');
        if (form && !form.checkValidity()) {
            markInvalidInputs();
            form.reportValidity();
            return;
        }
        const quotationData = {
            customerInfo,
            items: itemBasicInfoList.map((item, idx) => ({
                itemBasicInfo: item,
                itemSpecifications: itemSpecificationsRowsList[idx]
            })),
            discountInfo,
            pricing,
            companyInformation,
            termsAndConditions: currentTermsAndConditions, // Use current terms
            welcomeNote: messageLabels.welcomeNote,
            notes: messageLabels.notes
        }
        console.log("Quotation Data:", quotationData);
        await generatePDF(quotationData);
        // Show modal on success
        setShowModal(true);
    };

    // Modal state
    const [showModal, setShowModal] = useState(false);
    const modalRef = useRef(null);

    useEffect(() => {
        // Show the modal using Bootstrap's JS API
        const modalEl = document.getElementById('successModal');
        if (showModal) {
            if (modalEl) {
                const modal = bootstrap.Modal.getOrCreateInstance(modalEl);
                modal.show();
            }
        }
    }, [showModal]);

    return (
        <div className="container">
            <div>
                <div className="row align-items-center">
                    <div className="col-md-12 col-12">
                        <CompanyInfo companyInfo={companyInformation}></CompanyInfo>
                    </div>
                </div>
            </div>
            <form onSubmit={handleGeneratePDF} className="bg-white p-4 rounded shadow-sm">
                <CustomerDetail customerInfo={customerInfo} handleCustomerChange={handleCustomerChange}></CustomerDetail>
                {itemBasicInfoList.map((itemBasicInfo, idx) => (
                    <div key={idx} className="item-container">
                        <div className="row">
                            <div className="col-10 text-bold">
                                <div>Item - {idx + 1}</div>
                            </div>
                            {
                                itemBasicInfoList.length > 1 && (
                                    <div className="col-2 text-end">
                                        <a
                                            href="#"
                                            style={{ color: "red", fontSize: "14px" }}
                                            onClick={(e) => {
                                                e.preventDefault();
                                                handleDeleteItemRow(idx);
                                            }}
                                        >
                                            Delete
                                        </a>
                                    </div>
                                )
                            }
                        </div>
                        <ItemDetails
                            itemBasicInfo={itemBasicInfo}
                            rows={itemSpecificationsRowsList[idx]}
                            handleDelete={(rowIdx) => handleItemSpecificationDeleteRow(idx, rowIdx)}
                            handleAdd={() => handleItemSpecificationAddRow(idx)}
                            handleItemValueChange={(e) => handleItemValueChange(idx, e)}
                            handleItemSpecificationChange={(rowIdx, e) => handleItemSpecificationChange(idx, rowIdx, e)}
                        />
                    </div>
                ))}
                {/* Add more items button */}
                <div className="mb-3">
                    <a
                        href="#"
                        className="btn btn-outline-primary w-100"
                        onClick={(e) => {
                            e.preventDefault();
                            handleAddMoreItems();
                        }}
                    >
                        Add more items
                    </a>
                </div>
                {/* Single Discount and Pricing for all items */}
                <Discount discountInfo={discountInfo} change={handleDiscountChange} />
                <PricingSummary pricingInfo={pricing} />
                <div className="mt-4 border rounded p-3 mb-3">
                    <p>{messageLabels.notes}</p>
                </div>
                <TermsAndConditions 
                    termsAndConditions={currentTermsAndConditions} 
                    onTermsChange={handleTermsChange}
                />
                <div className="col-12">
                    <a
                        href="#"
                        className="btn btn-success w-100"
                        onClick={(e) => {
                            e.preventDefault();
                            handleGeneratePDF(e);
                        }}
                    >
                        Generate PDF
                    </a>
                </div>


            </form>
            <div className="mt-4">
                <Footer companyInfo={companyInformation} />
            </div>

            {/* ...Display section for generated data (if needed)... */}

            {/* Success Modal */}
            {showModal && (
                <SuccessPopupComponent
                    message="Quotation downloaded successfully."
                    onClose={() => setShowModal(false)}
                    modalId="successModal"
                    ref={modalRef}
                />
            )}
        </div>
    );
}

export default QuotationForm;
