//create a footer component that will be used in the app and fixed at the bottom of the page with some details like company name, address, phone number, email, and website. It should be responsive and look good on both mobile and desktop devices.
import React from 'react';

const Footer = ({ companyInfo }) => {
    return (
        <footer className="bg-light text-dark py-1">
            <div className="container">
                <div className="row">
                    <div className="col-md-6 col-sm-6 mb-1">
                        <p className="mb-1">
                            <strong>Website:</strong>{" "}
                            <a
                                target="_blank"
                                rel="noopener noreferrer"
                                href={`https://${companyInfo.website}`}
                            >
                                {companyInfo.website}
                            </a>
                        </p>
                    </div>
                    <div className="col-md-3 col-sm-6 mb-1">
                        <p className="mb-1"><strong>Manager:</strong> {companyInfo.manager}</p>
                    </div>
                    <div className="col-md-3 col-sm-6 mb-1">
                        <p className="mb-1"><strong>Email:</strong> {companyInfo.email}</p>
                    </div>
                </div>
            </div>
        </footer>
    );
};

export default Footer;
