import { useState } from "react";

const TermsAndConditions = ({ termsAndConditions, onTermsChange }) => {
    const [isEditing, setIsEditing] = useState(false);
    const [editedTerms, setEditedTerms] = useState(termsAndConditions.join('\n'));

    const handleEdit = () => {
        setIsEditing(true);
        setEditedTerms(termsAndConditions.join('\n'));
    };

    const handleSave = () => {
        const updatedTerms = editedTerms.split('\n').filter(term => term.trim() !== '');
        onTermsChange(updatedTerms);
        setIsEditing(false);
    };

    const handleCancel = () => {
        setIsEditing(false);
        setEditedTerms(termsAndConditions.join('\n'));
    };

    return (
        <div className="mt-4 border rounded p-3 mb-3">
            <div className="d-flex justify-content-between align-items-center mb-3">
                <h5>Terms and Conditions</h5>
                <div>
                    {!isEditing ? (
                        <button
                            type="button"
                            className="btn btn-outline-primary btn-sm"
                            onClick={handleEdit}
                        >
                            Edit
                        </button>
                    ) : (
                        <div>
                            <button
                                type="button"
                                className="btn btn-success btn-sm me-2"
                                onClick={handleSave}
                            >
                                Save
                            </button>
                            <button
                                type="button"
                                className="btn btn-secondary btn-sm"
                                onClick={handleCancel}
                            >
                                Cancel
                            </button>
                        </div>
                    )}
                </div>
            </div>
            
            {!isEditing ? (
                <ul>
                    {termsAndConditions.map((term, index) => (
                        <li key={index}>{term}</li>
                    ))}
                </ul>
            ) : (
                <textarea
                    className="form-control"
                    rows="10"
                    value={editedTerms}
                    onChange={(e) => setEditedTerms(e.target.value)}
                    placeholder="Enter each term on a new line"
                />
            )}
        </div>
    );
};

export default TermsAndConditions;