const CompanyHeader = ({ companyInfo }) => {
    // Define styles with media queries
    const styles = {
        container: {
            backgroundColor: "#f8f9fa",
        },
        logo: {
            width: "100px",
            height: "auto",
        },
        logoMobile: {
            width: "30px",
            height: "auto",
        },
        text: {
            fontSize: "16px",
        },
        textMobile: {
            fontSize: "10px",
        },
    };

    // Determine if the screen is mobile
    const isMobile = window.innerWidth <= 768;

    return (
        <div
            className="container-fluid p-2 border rounded mb-3"
            style={styles.container}>
            <div className="row align-items-center">
                {/* First Row: Logo and Company Info */}
                <div className="col-12 col-md-6 d-flex align-items-center mb-2 mb-md-0">
                    <div className="me-3">
                        <img
                            src="/quotation-web-live/images/logo.png"
                            alt={`${companyInfo.name} Logo`}
                            style={isMobile ? styles.logoMobile : styles.logo} // Apply media query styles
                        />
                    </div>
                    <div>
                        <h5
                            className="text-primary mb-1"
                            style={isMobile ? styles.textMobile : styles.text} // Apply media query styles
                        >
                            {companyInfo.name}
                            <span className="badge bg-info text-light ms-2">
                                GST: {companyInfo.gstNumber}
                            </span>
                        </h5>
                        <p
                            className="mb-1"
                            style={isMobile ? styles.textMobile : styles.text} // Apply media query styles
                        >
                            <strong>Specialist in:</strong> {companyInfo.specialty}
                        </p>
                        <p className="mb-1"><strong>Address:</strong> {companyInfo.address}</p>

                        <p
                            className="mb-1"
                            style={isMobile ? styles.textMobile : styles.text} // Apply media query styles
                        >
                            <strong>Phone:</strong> {companyInfo.phone}
                        </p>
                    </div>
                </div>

                {/* Second Row: Quotation Text */}
                <div className="col-12 col-md-6 text-md-end">
                    <h4
                        className="text-bold text-uppercase text-primary mt-2 mt-md-0"
                    >
                        Quotation
                    </h4>
                </div>
            </div>
        </div>
    );
};

export default CompanyHeader;
