const ItemSpecification = ({ rows, handleChange, handleDelete, handleAdd }) => {


    return (
        <div className="col-md-12 mt-4">
            <label className="form-label">Items Specifications / Dimensions</label>
            <div className="table-responsive">
                <table className="table table-bordered align-middle" id="dimensionsGrid">
                    <thead className="table-light">
                        <tr>
                            <th style={{ width: "40%" }}>Details</th>
                            <th style={{ width: "40%" }}>Value</th>
                            <th style={{ width: "20%" }}>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {rows.map((row, index) => (
                            <tr key={index}>
                                <td>
                                    <input
                                        type="text"
                                        name="detail"
                                        className="form-control"
                                        placeholder="Detail"
                                        value={row.detail}
                                        onChange={(e) => handleChange(index, e)}
                                    />
                                </td>
                                <td>
                                    <input
                                        type="text"
                                        name="value"
                                        className="form-control"
                                        placeholder="Value"
                                        value={row.value}
                                        onChange={(e) => handleChange(index, e)}
                                    />
                                </td>
                                <td>
                                    {rows.length > 1 && (
                                        <button
                                            type="button"
                                            className="btn btn-lg"
                                            onClick={() => handleDelete(index)}
                                        >
                                            <i className="bi bi-trash"></i>
                                        </button>
                                    )}
                                    {index === rows.length - 1 && (
                                        <button
                                            type="button"
                                            className="btn btn-lg"
                                            onClick={() => handleAdd(index)}
                                        >
                                            <i className="bi bi-building-add"></i>
                                        </button>
                                    )}
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
};

export default ItemSpecification;
