import ItemBasicInfo from "./ItemBasicInfo";
import ItemSpecification from "./ItemSpecification";
import { useState } from "react";

const ItemDetails = ({ itemBasicInfo, rows, handleDelete, handleAdd, handleItemValueChange, handleItemSpecificationChange }) => {


    return (
        <div className="row g-3 border rounded p-1 mb-1"
            style={styles.container}>

            <ItemBasicInfo itemBasicInfo={itemBasicInfo} handleItemValueChange={handleItemValueChange} />
            <ItemSpecification rows={rows} handleChange={handleItemSpecificationChange} handleDelete={handleDelete} handleAdd={handleAdd} ></ItemSpecification>
        </div>

    )
}

const styles = {
    container: {
        borderColor: "#dee2e6",
        backgroundColor: "#fafafa",
        marginTop: "10px",
    },
};


export default ItemDetails;