import jsPDF from "jspdf";
import PropTypes from "prop-types";

let currentY = 101; // Initial Y position for content
let doc = null; // Declare doc globally to use in checkPageBreak

const generatePDF = async (quotationData) => {
    // Reset doc and currentY for each PDF generation
    currentY = 101;
    doc = new jsPDF();

    // Header section with or without logo
    doc.setFontSize(16);
    doc.setFont(undefined, "bold");
    doc.text("Quotation", 105, 20, { align: "center" });
    // Add more content as needed

    printCompanyInfo(doc, quotationData.companyInformation);
    const logoData = await loadImageByUrl('quotation-web-live/images/logo.png'); // Load logo data
    window.logoDataForPDF = logoData; // Store logo data globally for later use
    await printImage(doc, logoData, 160, 35, 30, 30);
    // Divider line
    doc.setLineWidth(0.5);
    doc.line(20, 91, 190, 91);
    printQuotationDetails(doc);
    currentY += 5;
    printCustomerDetails(doc, quotationData.customerInfo);
    currentY += 5;
    printWelcomeNote(doc, quotationData.welcomeNote);
    // Divider line
    doc.setLineWidth(0.5);
    doc.line(20, 190, 190, 190);
    await printItemsDetails(doc, quotationData); // Make sure this is awaited if async
    printPriceSummary(doc, quotationData);
    currentY += 5;
    printNotes(doc, quotationData.notes);
    currentY += 5;
    printTermsAndConditions(doc, quotationData.termsAndConditions);
    currentY += 5;
    printBottomQuoteDate(doc); // Add generation date at the bottom
    currentY += 5;
    // Divider before signature
    doc.setLineWidth(0.5);
    doc.setDrawColor(0, 0, 0);
    doc.line(20, currentY, 190, currentY);
    currentY += 5;
    // Print signature with manager name
    await printSignature(doc, quotationData.companyInformation.manager);
    // Clear any global or static values after PDF generation
    window.logoDataForPDF = undefined;


    // Save the PDF
    printPDFWithDateAndCustomerName(quotationData.customerInfo.name);
};

const printPDFWithDateAndCustomerName = (name) => {
    const today = new Date();
    const day = String(today.getDate()).padStart(2, '0');
    const month = String(today.getMonth() + 1).padStart(2, '0'); // Months are 0-based
    const year = today.getFullYear();

    const formattedDate = `${day}-${month}-${year}`;
    doc.save(`${name}_quotation_${formattedDate}.pdf`);
}

// Function to check if we need a new page and add one if necessary
function checkPageBreak(requiredSpace = 20) {
    // Page height is 297mm for A4 (jsPDF default, unit: 'mm')
    // If you have no header/footer, you can use almost full height
    // Use a small top/bottom margin, e.g., 10mm each
    const usablePageHeight = 297 - 10 - 10; // 277mm
    if (currentY + requiredSpace > usablePageHeight) {
        doc.addPage();
        currentY = 10; // Reset to top margin of new page
        return true;
    }
    return false;
}

// Update printImage to accept x, y, width, height
const printImage = async (doc, imageData, x, y, maxWidth, maxHeight = null) => {
    if (imageData && imageData.dataURL) {
        try {
            let imageWidth = maxWidth;
            let imageHeight = imageWidth * (imageData.height / imageData.width);
            if (maxHeight && imageHeight > maxHeight) {
                imageHeight = maxHeight;
                imageWidth = imageHeight * (imageData.width / imageData.height);
            }
            doc.addImage(imageData.dataURL, 'PNG', x, y, imageWidth, imageHeight);
        } catch (error) {
        }
    }
}

const printCompanyInfo = (doc, companyInfo) => {
    // Company information on left side (matching HTML order)
    doc.setFontSize(11);
    doc.setFont(undefined, "normal");
    doc.text(companyInfo.name, 20, 35);
    doc.text(`Address: ${companyInfo.address}`, 20, 42);
    doc.text(`Specialist in: ${companyInfo.specialty}`, 20, 49);
    doc.text(`GST Number: ${companyInfo.gstNumber}`, 20, 56);
    doc.text(`Marketing Manager: ${companyInfo.manager}`, 20, 63);
    doc.text(`Email: ${companyInfo.email}`, 20, 70);
    doc.text(`Website: ${companyInfo.website}`, 20, 77);
    doc.text(`Phone: ${companyInfo.phone}`, 20, 84);
}

const printQuotationDetails = (doc) => {
    // Quotation Information Section
    checkPageBreak(30); // Check if we need space for the section
    doc.setFontSize(14);
    doc.setFont(undefined, "bold");
    doc.setTextColor(40, 116, 166); // Blue color for headers
    doc.text("Quotation Information", 20, currentY);
    currentY += 10;
    // Generate quotation ID and date
    const quotationId = `QT-${Date.now().toString().slice(-6)}`;
    const quotationDate = new Date().toLocaleDateString();
    // Quotation Information Table
    doc.setTextColor(0, 0, 0); // Reset to black
    doc.setFont(undefined, "normal");
    doc.setFontSize(10);

    const quotationInfo = [
        ["Quotation ID", quotationId],
        ["Quotation Date", quotationDate]
    ];
    quotationInfo.forEach((row, index) => {
        checkPageBreak(10); // Check before each row
        const rowY = currentY;
        // Draw light gray background for rows
        doc.setFillColor(250, 250, 250);
        doc.rect(20, rowY - 5, 170, 7, 'F');
        // Draw thinner border
        doc.setDrawColor(200, 200, 200);
        doc.setLineWidth(0.2);
        doc.rect(20, rowY - 5, 170, 7, 'S');
        doc.rect(20, rowY - 5, 60, 7, 'S'); // Separator line

        // Add text
        doc.setFont(undefined, "bold");
        doc.text(row[0], 22, rowY);
        doc.setFont(undefined, "normal");
        doc.text(row[1], 82, rowY);
        currentY += 8;
    });

}

const printCustomerDetails = (doc, customerInfo) => {
    // Customer Information Section
    checkPageBreak(40); // Check if we need space for the section
    doc.setFontSize(14);
    doc.setFont(undefined, "bold");
    doc.setTextColor(40, 116, 166); // Blue color for headers
    doc.text("Customer Information", 20, currentY);
    currentY += 10;

    // Customer Information Table
    doc.setTextColor(0, 0, 0); // Reset to black
    doc.setFont(undefined, "normal");
    doc.setFontSize(10);

    const customerData = [
        ["Customer Name", customerInfo.name || 'N/A'],
        ["Phone Number", customerInfo.phone || 'N/A'],
        ["Address", customerInfo.address || 'N/A']
    ];

    customerData.forEach((row, index) => {
        checkPageBreak(10); // Check before each row
        const rowY = currentY;
        // Draw light gray background for rows
        doc.setFillColor(250, 250, 250);
        doc.rect(20, rowY - 5, 170, 7, 'F');
        // Draw thinner border
        doc.setDrawColor(200, 200, 200);
        doc.setLineWidth(0.2);
        doc.rect(20, rowY - 5, 170, 7, 'S');
        doc.rect(20, rowY - 5, 60, 7, 'S'); // Separator line

        // Add text
        doc.setFont(undefined, "bold");
        doc.text(row[0], 22, rowY);
        doc.setFont(undefined, "normal");
        doc.text(row[1], 82, rowY);
        currentY += 8;
    });
}

const printWelcomeNote = (doc, welcomeNote) => {
    // Welcome Note Section
    if (welcomeNote) {
        doc.setFontSize(10);
        doc.setFont(undefined, "normal");
        doc.setTextColor(0, 0, 0);

        // Split the welcome note text to fit within page width
        const welcomeText = doc.splitTextToSize(welcomeNote, 170);
        const requiredSpace = welcomeText.length * 5;
        checkPageBreak(requiredSpace);

        doc.text(welcomeText, 20, currentY);
        currentY += requiredSpace + 10;
    }
}

const printItemsDetails = async (doc, quotationData) => {
    // Item Details Section
    checkPageBreak(50); // Check if we need space for the section
    doc.setFontSize(14);
    doc.setFont(undefined, "bold");
    doc.setTextColor(40, 116, 166); // Blue color for headers
    doc.text("Item Details", 20, currentY);
    currentY += 10;

    // Item Details Table
    doc.setTextColor(0, 0, 0); // Reset to black
    doc.setFont(undefined, "normal");
    doc.setFontSize(10);
    // Iterate over each item entry
    for (const [itemIndex, item] of quotationData.items.entries()) {
        const itemDetail = item.itemBasicInfo;
        const itemSpecifications = item.itemSpecifications;

        doc.setFillColor(255, 255, 204); // Light yellow background
        doc.setDrawColor(200, 200, 200); // Light gray border
        doc.setLineWidth(0.2);
        doc.rect(20, currentY - 5, 170, 10, 'FD'); // Full width with fill and border
        doc.setFontSize(11);
        doc.setFont(undefined, "bold");
        doc.setTextColor(0, 0, 0);
        doc.text(`Item ${itemIndex + 1}:`, 22, currentY);
        currentY += 10;

        // Table layout: 2 rows, 4 columns
        // Column widths: [itemName: 70, quantity: 30, amount: 35, totalPrice: 35]
        const colX = [20, 90, 120, 155];
        const colW = [70, 30, 35, 35];
        const rowH = 8;

        // Draw header row
        doc.setFont(undefined, "bold");
        doc.setFillColor(230, 240, 255);
        for (let i = 0; i < colX.length; i++) {
            doc.rect(colX[i], currentY, colW[i], rowH, 'F');
        }
        doc.setDrawColor(200, 200, 200);
        doc.setLineWidth(0.2);
        for (let i = 0; i < colX.length; i++) {
            doc.rect(colX[i], currentY, colW[i], rowH, 'S');
        }
        doc.text("Item Name", colX[0] + 2, currentY + 6);
        doc.text("Quantity", colX[1] + 2, currentY + 6);
        doc.text("Amount", colX[2] + 2, currentY + 6);
        doc.text("Total Price", colX[3] + 2, currentY + 6);

        // Draw value row
        currentY += rowH;
        doc.setFont(undefined, "normal");
        doc.setFillColor(250, 250, 250);
        for (let i = 0; i < colX.length; i++) {
            doc.rect(colX[i], currentY, colW[i], rowH, 'F');
        }
        doc.setDrawColor(200, 200, 200);
        doc.setLineWidth(0.2);
        for (let i = 0; i < colX.length; i++) {
            doc.rect(colX[i], currentY, colW[i], rowH, 'S');
        }
        doc.rect(colX[3], currentY, colW[3], rowH, 'S');
        doc.text(itemDetail.itemName || 'N/A', colX[0] + 2, currentY + 6);
        doc.text((itemDetail.quantity || '').toString(), colX[1] + 2, currentY + 6);
        doc.text(`Rs.${parseFloat(itemDetail.amount || '0').toFixed(2)}`, colX[2] + 2, currentY + 6);
        doc.text(`Rs.${parseFloat(itemDetail.totalPrice || '0').toFixed(2)}`, colX[3] + 2, currentY + 6);

        currentY += rowH + 10;

        // Print item specifications if available
        await printItemsSpecifications(doc, itemSpecifications, itemDetail.itemImage);
        currentY += 10;

        // Add spacing between items
        if (itemIndex < quotationData.items.length - 1) {
            currentY += 10;
        }
    }
};

const printItemsSpecifications = async (doc, itemSpecifications, itemImagePath) => {
    // Only render if there is at least one spec with detail or value
    const hasAnySpec = Array.isArray(itemSpecifications) &&
        itemSpecifications.some(spec =>
            (spec.detail && spec.detail.trim() !== "") ||
            (spec.value && spec.value.trim() !== "")
        );
    if (!hasAnySpec) {
        return;
    }
    // Product Specifications as a 2-column table: left 60% for specs, right for logo
    const tableX = 20;
    const tableW = 170;
    const leftColW = tableW * 0.7; // 70% width
    const rightColW = tableW * 0.3; // 30% width
    const rowH = 8;
    const logoMaxWidth = rightColW - 10;
    const logoMaxHeight = 20;

    // Only count non-empty specs for row count
    const nonEmptySpecs = itemSpecifications.filter(
        spec =>
            (spec.detail && spec.detail.trim() !== "") ||
            (spec.value && spec.value.trim() !== "")
    );

    // Always reserve enough height for logo (at least logoMaxHeight)
    const specRows = Math.max(nonEmptySpecs.length, Math.ceil(logoMaxHeight / rowH));
    const tableHeight = rowH * specRows;

    checkPageBreak(20 + tableHeight);
    doc.setFontSize(11);
    doc.setFont(undefined, "bold");
    doc.setTextColor(50, 50, 50); // Dark gray color
    doc.text("Item Specifications:", tableX, currentY);
    currentY += 2;

    // Draw table row
    doc.setTextColor(0, 0, 0);
    doc.setFontSize(10);
    doc.setFont(undefined, "normal");
    doc.setFillColor(250, 250, 250);
    doc.rect(tableX, currentY, leftColW, tableHeight, 'F');
    doc.rect(tableX + leftColW, currentY, rightColW, tableHeight, 'F');
    doc.setDrawColor(200, 200, 200);
    doc.setLineWidth(0.2);
    doc.rect(tableX, currentY, leftColW, tableHeight, 'S');
    doc.rect(tableX + leftColW, currentY, rightColW, tableHeight, 'S');

    // Print all non-empty specifications in left column, details: value format, line by line
    let specY = currentY + 6;
    nonEmptySpecs.forEach((spec, idx) => {
        const text = `${spec.detail ? spec.detail + ' - ' : ''} ${spec.value || ''}`;
        // Wrap text to fit within leftColW - 6 (padding)
        const wrapped = doc.splitTextToSize(text, leftColW - 6);
        wrapped.forEach((line, i) => {
            doc.text(line, tableX + 2, specY + i * rowH);
        });
        specY += rowH * wrapped.length;
    });

    // Print image in right column (centered vertically)
    const itemImage = await loadImageByUrl(itemImagePath);
    let logoX = tableX + leftColW + 5;
    let logoY = currentY + (tableHeight - logoMaxHeight) / 2;
    if (itemImage) {
        printImage(doc, itemImage, logoX, logoY, logoMaxWidth, logoMaxHeight);
    }
    currentY += tableHeight + 7;
};

const printPriceSummary = (doc, quotationData) => {
    // Price Summary Section
    checkPageBreak(70); // Check if we need space for the pricing section
    doc.setFontSize(14);
    doc.setFont(undefined, "bold");
    doc.setTextColor(40, 116, 166); // Blue color for headers

    doc.text("Price Summary", 20, currentY);
    currentY += 10;
    // Price Summary Table
    doc.setTextColor(0, 0, 0); // Reset to black
    doc.setFont(undefined, "normal");
    doc.setFontSize(10);

    let pricingData = [];
    const basePricingData = [
        ["Total price", `Rs.${parseFloat(quotationData.pricing.totalItemsPrice || '0').toFixed(2)}`],
        ["SGST Amount (9%)", `Rs.${parseFloat(quotationData.pricing.sgstAmount || '0').toFixed(2)}`],
        ["CGST Amount (9%)", `Rs.${parseFloat(quotationData.pricing.cgstAmount || '0').toFixed(2)}`],
        ["Final Price (With GST)", `Rs.${parseFloat(quotationData.pricing.finalPriceWithGst || '0').toFixed(2)}`]
    ];

    if (quotationData.discountInfo.priceAfterDiscount) {
        pricingData = [
            ...basePricingData.slice(0, 1), // Include "Total price"
            ["Any Discount Applied?", quotationData.discountInfo.isAnyDiscount],
            ["Discount Percentage", `${parseFloat(quotationData.discountInfo.discount || '0').toFixed(2)}%`],
            ["Price After Discount", `Rs.${parseFloat(quotationData.discountInfo.priceAfterDiscount || '0').toFixed(2)}`],
            ...basePricingData.slice(1) // Include SGST, CGST, and Final Price
        ];
    } else {
        pricingData = [
            ...basePricingData.slice(0, 1), // Include "Total price"
            ["Any Discount Applied?", 'Already Discounted'],
            ...basePricingData.slice(1) // Include SGST, CGST, and Final Price
        ];
    }
    pricingData.forEach((row, index) => {
        checkPageBreak(10); // Check before each pricing row
        const rowY = currentY;

        // Determine which row is the final price with GST based on discount status
        let finalPriceWithGstIndex, calculatedValueIndices;

        if (quotationData.discountInfo.priceAfterDiscount) {
            finalPriceWithGstIndex = 6; // "Final Price (With GST)" is at index 6 when discount is shown
            calculatedValueIndices = [3, 4, 5]; // Price After Discount, SGST, CGST
        } else {
            finalPriceWithGstIndex = 4; // "Final Price (With GST)" is at index 4 when no discount is shown
            calculatedValueIndices = [2, 3]; // SGST, CGST
        }

        // Highlight Final Price (With GST) with green background
        if (index === finalPriceWithGstIndex) {
            doc.setFillColor(212, 237, 218); // Light green for final price
        } else if (calculatedValueIndices.includes(index)) {
            doc.setFillColor(255, 243, 205); // Light yellow for calculated values
        } else {
            doc.setFillColor(250, 250, 250);
        }

        doc.rect(20, rowY - 5, 170, 7, 'F');
        // Draw thinner border
        doc.setDrawColor(200, 200, 200);
        doc.setLineWidth(0.2);
        doc.rect(20, rowY - 5, 170, 7, 'S');
        doc.rect(20, rowY - 5, 100, 7, 'S'); // Separator line

        // Add text
        doc.setFont(undefined, "bold");
        doc.text(row[0], 22, rowY);
        doc.setFont(undefined, index === finalPriceWithGstIndex ? "bold" : "normal"); // Bold for final price with GST
        if (index === finalPriceWithGstIndex) {
            doc.setFontSize(12); // Larger font for final price
        }
        doc.text(String(row[1]), 122, rowY); // Ensure value is a string
        doc.setFontSize(10); // Reset font size
        currentY += 8;
    });
}

const printNotes = (doc, notes) => {
    if (notes) {
        checkPageBreak(30); // Check space for notes
        doc.setFontSize(12);
        doc.setFont(undefined, "bold");
        doc.setTextColor(40, 116, 166); // Blue color for headers
        doc.text("Important Notes", 20, currentY);
        currentY += 8;

        doc.setFontSize(10);
        doc.setFont(undefined, "normal");
        doc.setTextColor(0, 0, 0);

        // Split the notes text to fit within page width
        const notesText = doc.splitTextToSize(notes, 170);
        const requiredSpace = notesText.length * 5;
        checkPageBreak(requiredSpace);

        // Add light background for notes
        doc.setFillColor(255, 252, 230);
        doc.rect(20, currentY - 3, 170, requiredSpace + 6, 'F');
        doc.setDrawColor(200, 200, 200);
        doc.setLineWidth(0.2);
        doc.rect(20, currentY - 3, 170, requiredSpace + 6, 'S');

        doc.text(notesText, 22, currentY + 2);
        currentY += requiredSpace + 15;
    }
}

const printTermsAndConditions = (doc, termsAndConditions) => {
    // Terms and Conditions Section
    if (termsAndConditions && termsAndConditions.length > 0) {
        checkPageBreak(30); // Check space for terms header
        doc.setFontSize(14);
        doc.setFont(undefined, "bold");
        doc.setTextColor(40, 116, 166); // Blue color for headers
        doc.text("Terms and Conditions", 20, currentY);
        currentY += 8;

        doc.setFontSize(9);
        doc.setFont(undefined, "normal");
        doc.setTextColor(0, 0, 0);

        termsAndConditions.forEach((term, index) => {
            const termText = `${index + 1}. ${term}`;
            const splitText = doc.splitTextToSize(termText, 170);
            const requiredSpace = splitText.length * 4;
            checkPageBreak(requiredSpace + 5); // Check space for each term
            doc.text(splitText, 20, currentY);
            currentY += requiredSpace;
        });
    }
}

const printBottomQuoteDate = (doc) => {
    // Generation Date
    checkPageBreak(30); // Check space for generation date and signature
    doc.setFontSize(10);
    doc.setFont(undefined, "italic");
    doc.setTextColor(100, 100, 100);
    const quotationDate = new Date().toLocaleDateString();
    doc.text(`Generated on: ${quotationDate}`, 20, currentY);
}

const printSignature = async (doc, managerName) => {
    // Add signature image above signature text
    let sigHeight = 0;
    try {
        // Load signature image as base64
        const sigImg = await loadImageByUrl('quotation-web-live/images/s.png');

        // Set signature image size and position
        const sigWidth = 40;
        sigHeight = sigImg.height * (sigWidth / sigImg.width);
        const sigX = 140;
        const sigY = currentY + 5;

        doc.addImage(sigImg.dataURL, 'PNG', sigX, sigY, sigWidth, sigHeight);

        // Add manager name just above signature text in gray
        doc.setFontSize(10);
        doc.setTextColor(120, 120, 120);
        doc.text(managerName, sigX, sigY + sigHeight + 0.5);

        // Place signature text just below the manager name (2px gap from image)
        doc.setFontSize(12);
        doc.setFont(undefined, "normal");
        doc.setTextColor(0, 0, 0);
        doc.text("Authorized Signature", sigX, sigY + sigHeight + 7);

        // Update currentY to below the signature section
        currentY = sigY + sigHeight + 14;
    } catch (e) {
        // If image fails, just show text and line
        doc.setFontSize(12);
        doc.setFont(undefined, "normal");
        doc.setTextColor(0, 0, 0);
        doc.text("Authorized Signature", 140, currentY + 20);
        doc.setLineWidth(0.3);
        doc.line(140, currentY + 25, 190, currentY + 25);
        // Add manager name in gray just above signature text
        doc.setFontSize(10);
        doc.setTextColor(120, 120, 120);
        doc.text(managerName, 140, currentY + 15);
        currentY += 37;
    }
}

const loadImageByUrl = async (imageUrl) => {
    if (!imageUrl) {
        return null;
    }
    // Try to load and add image
    let imageData = null;
    try {
        imageData = await getImageBase64(imageUrl);
    } catch (error) {
    }
    return imageData;
}

// Function to convert image to base64
const getImageBase64 = (src) => {
    return new Promise((resolve, reject) => {
        const img = new Image();
        img.crossOrigin = 'anonymous'; // Handle CORS
        img.onload = function () {
            const canvas = document.createElement('canvas');
            const ctx = canvas.getContext('2d');
            canvas.width = img.width;
            canvas.height = img.height;
            ctx.drawImage(img, 0, 0);
            try {
                const dataURL = canvas.toDataURL('image/png');
                resolve({ dataURL, width: img.width, height: img.height });
            } catch (e) {
                reject(e);
            }
        };
        img.onerror = () => reject(new Error('Image failed to load'));
        img.src = src;
    });
}

export default generatePDF;
