import React, { useState } from "react";
import QuotationForm from "./Screens/QuotationForm";
import "./App.css";
import data from "./core/data";
import Login from "./Screens/Login";

const securityKey = data.securityKey;

function App() {
  const [authenticated, setAuthenticated] = useState(false);
  const [inputPassword, setInputPassword] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    if (inputPassword === securityKey) {
      setAuthenticated(true);
    } else {
      setInputPassword("");
      alert("Invalid security key.");
    }
  };

  if (!authenticated) {
    return <Login inputPassword={inputPassword} setInputPassword={setInputPassword} handleSubmit={handleSubmit} />;
  }

  return <QuotationForm />;
}

export default App;