const Login = ({ inputPassword, setInputPassword, handleSubmit }) => {
    return (
        <div
            className="security-popup"
        >
            <form
                onSubmit={handleSubmit}
                style={{
                    background: "#fff",
                    padding: "2rem",
                    borderRadius: "8px",
                    boxShadow: "0 0 10px #222",
                    minWidth: "500px",
                }}
            >
                <h4 style={{ marginBottom: "1rem", color: "#333" }}>
                    Enter Security Key
                </h4>
                <input
                    type="password"
                    value={inputPassword}
                    onChange={(e) => setInputPassword(e.target.value)}
                    className="form-control mb-3"
                    placeholder="Security Key"
                    autoFocus
                    required
                />
                <button type="submit" className="btn btn-primary w-100">
                    Submit
                </button>
            </form>
        </div>
    );
}
export default Login;